import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'
//trades?symbol=BTCUSDT&interval=3m btcusdt@trade
export const CoinApi=createApi({
    reducerPath: 'coinApi',
    baseQuery: fetchBaseQuery({baseUrl:'https://fapi.binance.com'}),
    endpoints: (build)=>({
        getCoin: build.query<any,void>({
            query:()=>`/fapi/v1/klines?symbol=BTCUSDT&interval=1h`,
            async onCacheEntryAdded(
                arg,
                { updateCachedData, cacheDataLoaded, cacheEntryRemoved }
              ) {
                const ws = new WebSocket('wss://stream.binance.com:9443/ws/btcusdt@kline_1h')
                try {
                    await cacheDataLoaded
                    const listener = (event: MessageEvent) => {
                      const data = JSON.parse(event.data)
                      updateCachedData((draft) => {
                        draft.push(data)
                      })
                      console.log(data)
                    }
                    ws.onmessage=listener
                } catch {
                    console.log('error')
                }
                await cacheEntryRemoved
                ws.close()
              },
        })
    })
})
export const {useGetCoinQuery}=CoinApi