import React, { useEffect, useState } from 'react'
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import styles from './styles.module.css'
import ModalCopied from '../../../../ui/modalCopied/ModalCopied';
import { useGetCoinQuery } from '../../../../pages/MainPage/api/CoinApi';
const CoinName = () => {
  // const {data, isLoading, isError, isFetching, isSuccess}=useGetCoinQuery()
    const coin='Монета'
    const [isActiveModal, setIsActiveModal]=useState<boolean>(false)
    const copyText=()=>{
      navigator.clipboard.writeText(coin)
      setIsActiveModal(true)
      setTimeout(()=>{
        setIsActiveModal(false)
      }, 1000)
    }
   
  return (
    <div className={styles.wrap}>
        <p>{coin} <ContentCopyIcon onClick={copyText}/></p>
        <ModalCopied isActiveModal={isActiveModal}/>
    </div>
  )
}

export default CoinName